<template>
    <div id="home" class="base-page d-flex justify-center align-center">
        <v-container class="d-flex justify-center align-center">
            <div class="d-flex flex-column align-center">
                <p class="text-white text-h4" style="z-index: 10">
                    Hi, I'm <span @dblclick="setDialog('login')">Junia</span>.
                </p>
                <vue-typed-js :strings="['web programmer']">
                    <p
                        class="text-center text-white text-capitalize font-weight-bold text-h3 text-md-h1 typing"
                        style="z-index: 10"
                    ></p>
                </vue-typed-js>
                <p class="text-white text-h5" style="z-index: 10">
                    Based in Bali, Indonesia
                </p>
                <div class="" style="z-index: 10">
                    <v-btn x-large @click="toApp('Library')" class="mx-1">
                        <span class="font-weight-bold"> my design </span>
                    </v-btn>
                    <v-btn
                        x-large
                        outlined
                        dark
                        @click="setDialog('contact')"
                        class="mx-1"
                    >
                        <span class="font-weight-bold"> contact me </span>
                    </v-btn>
                </div>
            </div>
        </v-container>
        <LoginDialog />
        <ContactDialog />
        <BgIcon />
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "Home",
    components: {
        BgIcon: () =>
            import(
                /* webpackChunkName: "navbar" */ "@/components/CoreComponent/BgIcon.vue"
            ),
        LoginDialog: () =>
            import(
                /* webpackChunkName: "navbar" */ "@/components/CoreComponent/LoginDialog.vue"
            ),
        ContactDialog: () =>
            import(
                /* webpackChunkName: "navbar" */ "@/components/CoreComponent/ContactDialog.vue"
            ),
    },

    mounted: function () {
        let elHtml = document.getElementsByTagName("html")[0];
        elHtml.style.overflowY = "hidden";
    },

    destroyed: function () {
        let elHtml = document.getElementsByTagName("html")[0];
        elHtml.style.overflowY = null;
    },

    computed: {
        ...mapGetters({
            bgIcon: "getBgIcon",
        }),
    },

    data: () => ({
        //
    }),

    methods: {
        ...mapActions({
            updateDialogLogin: "updateDialogLogin",
            updateDialogContact: "updateDialogContact",
        }),
        toApp(q) {
            this.$router.push({ name: q });
        },
        setDialog(q) {
            switch (q) {
                case "login":
                    this.updateDialogLogin(true);
                    break;
                case "contact":
                    this.updateDialogContact(true);
                    break;
                default:
                    break;
            }
        },
    },
};
</script>

<style scoped>
#home {
    /* background-image: url('~@/assets/portofolio-one-page/bg-hero.jpg'); */
    /* background-size: cover; */
    height: 100vh;
    background-color: #2c2c2c;
    overflow: hidden;
}
</style>