export default {
    state: {
        currentUser: '',
        isLogin: false,
        userVerified: '',
    },
    getters: {
        getCurrentUser: state => {
            return state.currentUser;
        },
        getIsLogin: state => {
            return state.isLogin;
        },
    },
    mutations: {
        SET_CURRENT_USER: (state, payload) => state.currentUser = payload,
        SET_IS_LOGIN: (state, payload) => state.isLogin = payload,
    },
    actions: {
        changeIsLogin({ commit }, payload) {
            commit('SET_CURRENT_USER', payload)
            commit('SET_IS_LOGIN', payload.isLogin)
        },
    }
};