import Vue from 'vue'
import VueRouter from 'vue-router'
// import firebase from 'firebase';
import { fb } from "@/components/firebaseInit";
import Home from '../views/Home.vue'
// Layout Admin
import Layout from '../views/Admin/Layout/Layout.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home,
        meta: {
            requiresGuest: true
        }
    },
    {
        path: '/library',
        name: 'Library',
        component: () => import(/* webpackChunkName: "chat-app" */ '../views/LibraryApp.vue')
    },
    {
        path: '/chat-app',
        name: 'ChatApp',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "chat-app" */ '../views/ChatApp.vue')
    },
    {
        path: '/portofolio-one-page',
        name: 'PortofolioOnePage',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "chat-app" */ '../views/PortofolioOnePage.vue')
    },
    {
        path: '/digital-agency',
        name: 'DigitalAgency',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "chat-app" */ '../views/DigitalAgency.vue')
    },
    {
        path: '/shop-port',
        name: 'ShopPort',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "chat-app" */ '../views/ShopPort/ShopPort.vue')
    },
    {
        path: '/qr-code',
        name: 'QrCode',
        component: () => import(/* webpackChunkName: "chat-app" */ '../views/QrCode.vue')
    },
    {
        path: '',
        component: Layout,
        redirect: 'noredirect',
        hidden: true,
        children: [{
            path: 'dashboard',
            component: () => import( /* webpackChunkName: "about" */ '../views/Admin/Dashboard/Index.vue'),
            name: 'Dashboard',
            meta: {
                // requiresAuth: true,
                title: 'Dashboard',
            }
        }]
    },
    {
        path: '',
        component: Layout,
        redirect: 'noredirect',
        hidden: true,
        children: [
            {
                path: 'inbox',
                component: () => import( /* webpackChunkName: "about" */ '../views/Admin/Inbox/Index.vue'),
                name: 'Inbox',
                meta: {
                    title: 'Inbox Mails',
                }
            },
            {
                path: '/:slug/read-more',
                component: () => import( /* webpackChunkName: "about" */ '../views/Admin/Inbox/ReadMail.vue'),
                name: 'ReadMail',
                meta: {
                    title: 'Read Mail',
                }
            }
        ],
        meta: {
            requiresAuth: true,
        },
    },
    {
        path: '',
        component: Layout,
        redirect: 'noredirect',
        hidden: true,
        children: [
            {
                path: 'project',
                component: () => import( /* webpackChunkName: "about" */ '../views/Admin/Project/Index.vue'),
                name: 'Project',
                meta: {
                    // requiresAuth: true,
                    title: 'Project',
                },
            },
            {
                path: 'desciption',
                component: () => import( /* webpackChunkName: "about" */ '../views/Admin/Project/Description.vue'),
                name: 'Description',
                meta: {
                    // requiresAuth: true,
                    title: 'Description',
                },
            },
        ],
        meta: {
            requiresAuth: true,
        },
    },
    {
        path: '',
        component: Layout,
        redirect: 'noredirect',
        hidden: true,
        children: [{
            path: 'profile',
            component: () => import( /* webpackChunkName: "about" */ '../views/Admin/Profile/Index.vue'),
            name: 'Profile',
            meta: {
                // requiresAuth: true,
                title: 'Profile',
            }
        }],
        meta: {
            requiresAuth: true,
        },
    },
    {
        path: '',
        component: Layout,
        redirect: 'noredirect',
        hidden: true,
        children: [{
            path: 'notification',
            component: () => import( /* webpackChunkName: "about" */ '../views/Admin/Notification/Index.vue'),
            name: 'Notification',
            meta: {
                // requiresAuth: true,
                title: 'Notification',
            }
        }],
        meta: {
            requiresAuth: true,
        },
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
    scrollBehavior() {
        // console.log(to)
        // console.log(from)
        // console.log(savedPosition)
        return {
            x: 0,
            y: 0
        }
    }
})

// Nav Guard
router.beforeEach((to, from, next) => {
    // Check for requiresAuth guard
    if (to.matched.some(record => record.meta.requiresAuth)) {
        // Check if NO logged user
        if (!fb.auth().currentUser) {
            // Go to login
            next({
                path: '/',
                query: {
                    redirect: to.fullPath
                }
            });
        } else {
            // Proceed to route
            next();
        }
    } else if (to.matched.some(record => record.meta.requiresGuest)) {
        // Check if NO logged user
        if (fb.auth().currentUser) {
            // Go to login
            next({
                path: '/dashboard',
                query: {
                    redirect: to.fullPath
                }
            });
        } else {
            // Proceed to route
            next();
        }
    } else {
        // Proceed to route
        next();
    }
});

export default router
