export default {
    state: {
        projectType: [],
        projectDataAll: [],
        projectData: [],
        projectResetPage: false,
        filterProjectType: "all",
    },
    getters: {
        getProjectType: state => {
            return state.projectType
        },
        getProjectData: state => {
            return state.projectData;
        },
        getProjectResetPage: state => {
            return state.projectResetPage;
        },
    },
    mutations: {
        SET_ALL_PROJECT_TYPE(state, payload) {
            state.projectType = payload;
        },
        SET_ALL_PROJECT(state, payload) {
            state.projectDataAll = payload;
        },
        SET_PROJECT(state, payload) {
            state.projectData = payload;
        },
        SET_PROJECT_RESET_PAGE(state) {
            state.projectResetPage = !state.projectResetPage
        },
    },
    actions: {
        setAllProjectType({ commit }, payload) {
            commit('SET_ALL_PROJECT_TYPE', payload)
        },
        setAllProject({ commit }, payload) {
            commit('SET_ALL_PROJECT', payload)
        },
        setDisplayProjectType({ commit, state, dispatch}, payload) {
            // reset pgae to 1
            dispatch('projectResetPage')
            if (payload == 'all') {
                let data = state.projectDataAll
                commit('SET_PROJECT', data);
            }
            else {
                let data = state.projectDataAll.filter(value => value.code == payload);
                commit('SET_PROJECT', data);
            }
            state.filterProjectType = payload
        },
        projectResetPage({ commit }) {
            commit('SET_PROJECT_RESET_PAGE')
        }
    }
};