const state = {
    navDrawerStatus: false,
};

const getters = {
    getNavDrawerStatus: (state) => state.navDrawerStatus,
}

const actions = {
    updateNavDrawerStatus({ commit }, payload) {
        commit('SET_NAV_DRAWER_STATUS', payload)
    }
}

const mutations = {
    SET_NAV_DRAWER_STATUS: (state, payload) => state.navDrawerStatus = payload,
}


export default {
    state,
    getters,
    actions,
    mutations
}